import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/lib/clone';
import createId from '@/lib/createId';
import router from '@/router';
//  store  vue.prototype 
Vue.use(Vuex);


const store = new Vuex.Store({
  // 
  state: {
    recordList: [] as RecordItem[],

    tagList: [] as Tag[],

    currentTag: undefined,
    createRecordError: null
  } as RootState,


  // 
  mutations: {
    createRecord(state, record: RecordItem) {
      const recordClone: RecordItem = clone(record);
      recordClone.createAt = recordClone.createAt ||  new Date().toISOString();
      state.recordList && state.recordList.push(recordClone);
      store.commit('saveRecord');
      //saveRecord();
    },
    saveRecord(state) {         // 
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
    },
    fetchRecords(state) {        // 
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]');
    },


    fetchTags(state) {
      // 
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
      if (!state.tagList || state.tagList.length === 0) {
        store.commit('createTags', '');
        store.commit('createTags', '');
        store.commit('createTags', '');
        store.commit('createTags', '');
      }
    },
    createTags(state, name: string) {
      state.createRecordError = null
      const names = state.tagList.map(item => item.name);

      if (names.indexOf(name) >= 0) {
        // window.alert('');
        state.createRecordError = new Error('tag name duplicated');
        return ;
      }
      const id = createId().toString();
      state.tagList.push({id, name: name});
      store.commit('saveTags');
      return 'success';
    },
    saveTags(state) {         // 
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    },
    findTag(state, id: string) {
      let tag: Tag;
      tag = state.tagList.filter(t => t.id === id)[0];
      return tag;
    },
    setCurrentTag(state, id: string) {
      state.currentTag = state.tagList.filter(t => t.id === id)[0];
    },
    updateTag(state, payload: { id: string, name: string }) {
      const {id, name} = payload;

      const idList = state.tagList.map(item => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map(item => item.name);
        if (names.indexOf(name) >= 0) {
          window.alert('');
        } else {
          //   bug
          const tag = state.tagList.filter(item => item.id === id)[0];
          tag.name = name;

          store.commit('saveTags');
        }

      }
    },

    removeTag(state, id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i;
          break;
        }
      }
      if (index >= 0) {
        state.tagList.splice(index, 1);
        store.commit('saveTags');
        window.alert('');
        router.back();
      } else {
        window.alert('');
      }

    },
  },

  // 
  actions: {},

});

// 
// store.commit('name')

export default store;